.Details {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Details .Text {
  padding: 1em;
  margin-left: 1em;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.Details .Name {
  font-size: 1.1em;
}

.Details .Location {
  font-size: 1.1em;
}

/* Desktops and laptops ----------- */
@media only screen
and (min-width : 600px) {
  .Details {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }
}



