.DataTable {
  padding-top: 1em;
  padding-bottom: 1em;
  margin-bottom: 1em;
  font-size: 1em;
}

.DataTable .TableHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

@media (max-width: 400px) {
  .DataTable {
    display: block;
  }
  .DataTable .TableHeader {
    -ms-flex-direction: row;
        flex-direction: row;
  }
}

/* Desktops and laptops ----------- */
@media only screen
and (min-width : 600px) {
  .DataTable {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  .DataTable .TableHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



